<template>
  <main class="app">
    <h1 class="app__title">{{$root.localization.title[$root.lang]}}</h1>
    <button class="app__chek-lang"
            @click.stop="handlerChekLang"
    >{{lang}}</button>
    <add-new-feedback  class="app__add-new"></add-new-feedback>
    <feedback-list class="app__list-feedback" 
    :feedbacks="feedbacks"
    :isClickTanos="isClickTanos"
    ></feedback-list>
  </main>
</template>

<script>
import AddNewFeedback from './components/AddNewFeedback.vue'
import FeedbackList from './components/FeedbackList'
import Firebase from 'firebase'
import { setTimeout } from 'timers';

const config = {databaseURL: 'https://bookfeedback-c9a4d.firebaseio.com'};
const feedbackDB = Firebase.initializeApp(config).database().ref('bookfeedback'); // ПОДКЛЮЧЕНИЕ К ФАЕРБЭЙС

export default {
  data() {
    return {
      feedbacks: {},
      isClickTanos: false,
      lang: 'русский',
    }
  },
  components: {
    AddNewFeedback,
    FeedbackList
  },
  methods:{
    handlerChekLang(){
      if(this.lang == 'русский'){
        this.lang = 'english';
        this.$root.lang = 'en'
      }else if(this.lang == 'english'){
        this.lang = 'русский';
        this.$root.lang = 'ru'
      }
    },
    addNewItemInDB(item){
      feedbackDB.push(item, ()=>console.log('add new record'))
    },
    deleteFullInDB(){
      feedbackDB.set({}, ()=>console.log('total genocide'))
    },
    clickTanos(){
      this.isClickTanos = true;
      setTimeout(() => this.isClickTanos = false, 4000); // вернуть все обратно))
    }
  },
  firebase: {
    feedbacks: feedbackDB // добавляем ссылку на фаербэйс в свойства вью
  },
}
</script>

