<template>
  <section class="list-feedback">
    <h2 class="list-feedback__title">{{$root.localization.reviews[$root.lang]}}</h2>
    <pagination  class="list-feedback__pagination" :feedbacks="feedbacks"></pagination>
    <ul class="list-feedback__list">
        <feedback-item class="list-feedback__item"
        v-for="(feedback, index) in visibleFeedbacks"
        :key="index" 
        :item="feedback"
        ></feedback-item>
    </ul>
  </section>
</template>

<script>
import Pagination from './Pagination.vue'
import FeedbackItem from './FeedbackItem.vue'

export default {
  data() {
    return {
      visibleFeedbacks:[],
    }
  },
  methods: {
  },
  components:{
    Pagination,
    FeedbackItem
  },
  watch:{
    isClickTanos(){
      if(this.isClickTanos){   // рандомно сносим половину записей
        document.querySelectorAll('.list-feedback__item').forEach(
          item =>{ 
                  if(Math.random() >= 0.5) { 
                    item.classList.add('clickTanos-transition');
                    item.classList.add('clickTanos-active')
                    setTimeout(()=> item.style.display = 'none', 1500)
                  }
                    // setTimeout( () => this.visibleFeedbacks.splice(i, 1), 3000)}
                });
      } 
    }
  },
  props: {
    feedbacks: Object,
    isClickTanos: Boolean,
  }
}
</script>

<style scoped lang="scss">
  $color-BG: #F6F6f6;
  $color-item:#F6F6f6;
  $color-line: rgb(144, 146, 148);
  $color-btn:#67ad66;
  $color-star:#296eb1;
 
  .list-feedback{
    width: 100%;
    border: 1px solid $color-line;
    border-radius: 3px;
    &__title{
      margin: 0;
      padding: 15px;
      background-color: $color-BG;
      border-bottom: 1px solid $color-line;
      font-weight: 500;
    }
    &__list{
      display: flex;
      flex-direction: column;
      margin: 0;
      padding: 20px;
    }
    &__item:nth-child(2n){
      background-color:  $color-item;
    }
  }
  .clickTanos-transition {
    transition: all 1.2s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .clickTanos-active{ //исчезновение
    transform: translateX(20px);
    opacity: 0;
  }
</style>
